@keyframes text_gradient {
  0% {
    background-position: 0% 50%;
  }

  100% {
    background-position: 100% 50%;
  }
}

@keyframes fadeIn_right {
  from {
    opacity: 0;
    transform: translateX(200px);
  }

  to {
    opacity: 1;
    transform: translateY();
  }
}

@keyframes fadeIn_left {
  from {
    opacity: 0;
    transform: translateX(-200px);
  }

  to {
    opacity: 1;
    transform: translateY();
  }
}

@keyframes fadeIn_right {
  from {
    opacity: 0;
    transform: translateX(200px);
  }

  to {
    opacity: 1;
    transform: translateY();
  }
}

#about {
  position: relative;
  min-height: 100vh;
  background: url('../../public/about_bg_img.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  filter: brightness(90%);

}

/* 배경 그림 흐리게 하는 법 */
#about::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: -1;
}

.about_wrap {
  display: flex;
  align-items: center;
  justify-content: center;
}
.about_head{
  position: relative;
  right: 40px;
}

.about_head p{
  line-height: 3rem;
  font-size: 3rem;
  font-weight: 500;
  user-select: none;
  animation: text_gradient 8s linear infinite alternate;
  background: linear-gradient(90deg, rgb(208, 209, 142) 0%, rgb(255, 255, 255) 25%, rgb(194, 215, 118) 50%, rgb(255, 255, 255) 75%, rgb(252, 255, 49) 100%);
  background-size: 1000% 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.about_head p span {
  font-size: 4rem;
  font-weight: 900;
}

.about_infobox{
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin-top: 30px;
  border-radius: 8px;
  left: 22%;
  width: 300px;
  height: 450px;
  background-color: rgb(0,0,0,0.3);
  color: antiquewhite;
  font-size: 1rem;
  line-height: 1.5rem;
}

.about_infobox img{
  width: 160px;
  height: 240px;
  border-radius: 5px;
}

/* .about_infobox .line{
  width: 50%;
} */

.about_body {
  position: relative;
  max-width: 700px;
  top: 50px;
  font-size: 1rem;
  font-weight: 500;
  color: white;
  line-height: 1.9rem;
  padding: 10px;
  animation: fadeIn_right 2s ease-out;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.6); /* 배경색 흐리게 하기 */
}

.about_body ul li {
  position: relative;
  display: inline-block;
  border: 1px solid white;
  margin-right: 1rem;
  margin-top: 1rem;
  padding: 6px 10px;
  border-radius: 7px;
  color: rgb(216, 224, 64);
  font-weight: bold;
}

.about_body .more {
  display: inline-block;
  margin-top: 20px;
  padding: 8px 35px;
  font-size: 0.9rem;
  font-weight: bold;
  color: black;
  background-color: white;
  cursor: pointer;
  border-radius: 4px;
}
@media screen and (max-width: 1200px){
  .about_wrap {
    display: flex;
    flex-direction: column;
  }
  .about_head{
    bottom: 0;
    right: 0;
  }
  .about_infobox{
    display: none;
  }
}
@media screen and (max-width: 600px) {
  #about {
    padding: 20px;
  }
  
  .about_head {
    bottom: 0;
    right: 0;
    text-align: center;
  }
  
  .about_head p {
    font-size: 1.6rem; 
    line-height: 2rem;
  }
  
  .about_head p span {
    font-size: 2rem;
  }
  
  .about_body {
    max-width: 100%;
    padding: 20px; 
    font-size: 0.9rem;
    line-height: 1.5rem;
    border: 1px solid white;
  }
  
  .about_body ul li {
    margin-right: 0.5rem;
    padding: 5px 8px;
    font-size: 0.8rem;
  }
  
  .about_body .more {
    padding: 6px 20px;
    font-size: 0.8rem;
  }
  .about_infobox{
    display: none;
  }
}



