#stacks {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  min-height: 100vh;
  background: url('../../public/stack_bg.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  padding: 0.5rem;
}

.stacks_wrap {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.stacks_wrap span{
  margin: 0 auto;
  margin-top: 55px;
  font-size: 3rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.5);
}

.stack_box > div:first-child{
  text-align: center;
  margin-bottom: 2rem;
}

.stack_box .tit{
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 5px;
  border-radius: 5px;
}

.stack_box ul {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 20px 10px;
  box-sizing: border-box;
}

/* .stack_box ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
} */

.stack_box ul li {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 400px;
  border: 1px solid rgb(183, 183, 197, 0.2);
  border-radius: 10px;
  overflow: hidden;
  list-style: none;
  box-sizing: border-box;
  color: rgb(255,255,255,0.7);
  padding: 5px;
}

.stack_box ul li img {
  width: 50px;
}


@media screen and (max-width: 1185px){
  .stack_box ul{
    justify-content: center;
  }
}

