.project{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}

.item_wrap .img_wrap{
  width: 800px;
  height: auto;
  /* background-color: green; */
}

.item_wrap .img_wrap .main_img > img{
  width: 800px;
  height: auto;
  border: 1px solid black;
}

.item_wrap .img_wrap .sub_img_wrap{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.item_wrap .img_wrap .sub_img>img{
  margin-right: 20px;
  width: 130px;
  height: 64px;
  background-color: black;
}

.info_wrap{
  width: 800px;
  height: auto;
  line-height: 25px;
  /* background-color: beige; */
}

.info_wrap .info_tit{
  font-size: 1.7rem;
  font-weight: 700;
}

.info_wrap .info_sub_tit{
  font-size: 0.9rem;
  font-weight: 300;
  color: #90CBFB;
}


.tag_wrap{
  display: flex;
  list-style: none;
  margin-top: 20px;
}

.tag_wrap .tag{
  padding: 8px 15px;
  margin-right: 10px;
  text-align: center;
  background-color: gray;
  border-radius: 7px;
}

.tag_wrap .tag.last{
  margin-right: 0;
}

.button_wrap{
  margin-top: 40px;

}
.button_wrap a{
  margin-right: 20px;
  text-decoration: none;
  color: white;
  padding: 8px 15px;
  text-align: center;
  background-color: gray;
  border-radius: 7px;

}

.button_wrap a.last{
  margin-right: 0;
}

.custom-next {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  text-align: center;
  line-height: 30px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 10;
  font-size: 1.5rem;
}

.custom-next {
  right: 1px;
  top: 20px;
}


@media (max-width: 819px) {
  .item_wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 819px;
  }

  .item_wrap .img_wrap {
    width: 80%;
    max-width: 819px;
    height: auto;
  }

  .item_wrap .img_wrap .main_img > img {
    width: 100%;
    height: auto;
    max-height: 250px;
  }

  .item_wrap .img_wrap .sub_img_wrap {
    justify-content: center;
  }

  .item_wrap .img_wrap .sub_img > img {
    margin-right: 10px;
    width: 80px;
    height: auto;
  }

  .info_wrap {
    width: 80%;
    max-width: 819px;
    padding: 10px;
    height: auto;
  }

  .info_wrap .info_tit {
    font-size: 1.5rem;
  }

  .info_wrap .info_sub_tit {
    font-size: 1rem;
  }
  .info_wrap .info_main {
    font-size: 16px;
    line-height: 1.9rem;
  }


  .tag_wrap .tag {
    margin-bottom: 10px;
  }
  .tag_wrap{
    display: flex;
    list-style: none;
    margin-top: 20px;
  }


  .button_wrap a {
    display: inline-block;
    margin-bottom: 10px;
  }

  .custom-next {
    top: 10px;
  }
}



@media (max-width: 680px) {
  .item_wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 680px;
  }

  .item_wrap .img_wrap {
    width: 72%;
    max-width: 680px;
    height: auto;
  }

  .item_wrap .img_wrap .main_img > img {
    width: 100%;
    height: auto;
    max-height: 250px;
  }

  .item_wrap .img_wrap .sub_img_wrap {
    justify-content: center;
  }

  .item_wrap .img_wrap .sub_img > img {
    margin-right: 10px;
    width: 80px;
    height: auto;
  }

  .info_wrap {
    width: 72%;
    max-width: 680px;
    padding: 10px;
    height: auto;
  }

  .info_wrap .info_main {
    font-size: 16px;
    line-height: 1.9rem;
  }

  .info_wrap .info_tit {
    font-size: 1.5rem;
  }

  .info_wrap .info_sub_tit {
    font-size: 0.8rem;
  }


  .tag_wrap .tag {
    margin-bottom: 10px;
  }
  .tag_wrap{
    display: flex;
    list-style: none;
    margin-top: 20px;
  }


  .button_wrap a {
    display: inline-block;
    margin-bottom: 10px;
  }

  .custom-next {
    top: 10px;
  }
}



@media (max-width: 480px) {
  .item_wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 480px;
  }

  .item_wrap .img_wrap {
    width: 72%;
    max-width: 480px;
    height: auto;
  }

  .item_wrap .img_wrap .main_img > img {
    width: 100%;
    height: auto;
    max-height: 250px;
  }

  .item_wrap .img_wrap .sub_img_wrap {
    justify-content: center;
  }

  .item_wrap .img_wrap .sub_img > img {
    margin-right: 10px;
    width: 60px;
    height: auto;
  }

  .info_wrap {
    width: 80%; /* 부모 컨테이너의 너비의 90%로 설정 */
    max-width: 480px; /* 최대 너비 제한 */
    padding: 15px;
    height: auto;
  }

  .info_wrap .info_tit {
    font-size: 1.2rem;
  }

  .info_wrap .info_sub_tit {
    font-size: 0.7rem;
  }

  .info_wrap .info_main {
    font-size: 0.8rem;
  }

  .tag_wrap {
    flex-wrap: wrap;
  }

  .tag_wrap .tag {
    margin-bottom: 5px;
    font-size: 0.8rem;
  }

  .button_wrap {
    margin-top: 20px;
  }

  .button_wrap a {
    display: inline-block;
    padding: 5px 10px;
    font-size: 0.8rem;
  }

  .custom-next {
    position: absolute;
    top: 50%;
    width: 20px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    text-align: center;
    line-height: 20px;
    border-radius: 50%;
  }
  .custom-next {
    top: 6px;
  }
}



