.nav_bar {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 8px 12px;
  color: black;
  z-index: 999;

}

.home_icon img{
  width: 35px;
  margin-left: 5px;
}

.home_icon img:hover{
  transform: scale(1.2);
}

.ham_icon img{
  display: none;
  position: absolute;
  top: 10px;
  right: 15px;
  width: 35px;
}

.nav_menu{
  display: flex;
  align-items: center;
  list-style: none;
}

.nav_menu li{
  padding: 8px 12px;
  font-weight: 600;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
  margin-left: 1px;
  border-radius: 5px;
}

.nav_menu li:hover{
  background-color: white;
  color: black;
  border-radius: 4px;
}



@media screen and (max-width: 600px){
  .nav_bar{
    flex-direction: column;
    align-items: flex-start;
    background-color:  black
  }
  .nav_menu{
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .nav_menu li{
    display: none;
    width: 100%;
    text-align: center;
    background-color: black;
    color: rgba(255, 255, 255, 0.6);
  }
  .nav_menu.open li{
    display: block;
  }
  .nav_menu li:hover{
    background-color: black;
    color: white;
    border-radius: 4px;
  }
  .ham_icon img{
    display: block;
    z-index: 999;
  }
}