@keyframes scroll_right{
  0%{
    transform:rotate(-90deg) translateY(0px);
  }
  50%{
    transform:rotate(-90deg) translateY(20px)
  }
  100%{
    transform:rotate(-90deg) translateY(0px);
  }
}
@keyframes scroll_left{
  0%{
    transform:rotate(90deg) translateY(0px);
  }
  50%{
    transform:rotate(90deg) translateY(20px)
  }
  100%{
    transform:rotate(90deg) translateY(0px);
  }
}

#works{
  display: flex;
  flex-direction: column;
  justify-content: center; /* 수직 중앙 정렬 */
  min-height: 100vh;
}


.works_wrap {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.works_wrap h1{
  margin-top: 55px;
  text-align: center;
}
.works_wrap h6{
  display: none;
}

.works_wrap .item_container{
  display: flex;
  align-items: center;
  margin-top: -15px;
}


.works_wrap .item_nav_prev{
  position: absolute;
  left: 100px;
  width: 100px;
  height: 100px;
  background-image: url('/public/down-arrow.png');
  background-size: 100% 100%;
  animation: scroll_left 1.5s ease-in-out infinite;
  z-index: 10;
}

.works_wrap .item_nav_next{
  position: absolute;
  right: 100px;
  width: 100px;
  height: 100px;
  background-image: url('/public/down-arrow.png');
  background-size: 100% 100%;
  animation: scroll_right 1.5s ease-in-out infinite;
  z-index: 10;
}

@media screen and (max-width:1180px){
  .works_wrap .item_nav_prev,.item_nav_next{
    display: none;
  }

  .works_wrap h6{
    display: block;
    margin-top: 10px;
    text-align: center;
  }
  
}


