/* 전체 모달 스타일 */
.modal {
  position: fixed; 
  display: none; 
  justify-content: center;
  align-items: center;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto; 
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1; 
}

.modal.show {
  display: flex;
}

.modal-content {
  position: relative;
  background: url('../../public/modal_img.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  margin: 20px;
  width: 90%;
  height: 90%; 
  border-radius: 10px;
}

/* 닫기 버튼 */
.close {
  color: white;
  float: right;
  font-size: 28px;
  font-weight: bold;
  margin: 20px 20px;
}

.close:hover, .close:focus {
  cursor: pointer;
  transform: scale(1.1);
}

/* 모달 헤더 */
.modal_header {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.modal_header .select_list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none; 
  padding: 0;
  margin-top: 5rem;
}

.modal_header .select_list li {
  background-color: white;
  color: black;
  margin: 0 10px;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.modal_header .select_list li:hover{
  background-color: black;
  color: white;
  border-radius: 4px;
}

/* 탭 콘텐츠 */
.contents {
  flex: 1;
  overflow: auto;
}

/* 각 탭의 내용 */
.my_intro {
  position: relative;
  width: 65%;
  margin: 20px auto;
  text-align: left; 
  line-height: 1.65;
  padding: 20px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.85);
  border: 1px solid yellow;
}

@media screen and (max-width: 1024px) {
  .modal_header .select_list {
    margin-top: 10px;
  }
  .my_intro {
    width: 90%;
  }
}

@media screen and (max-width: 768px) {
  .modal-content {
    width: 100%;
    margin: 0;
  }

  .modal_header .select_list li {
    margin: 0 10px;
    padding: 1px 2px;
    font-size: 1rem;
  }
}

@media screen and (max-width: 480px) {
  .modal_header .select_list {
    margin-top: 0;
  }
  .modal_header .select_list li {
    margin: 0 0.5rem;
    padding: 0px;
    font-size: 0.8rem;
  }
}
