@keyframes scrolldown {
  0% {
    top: 0px;
    opacity: 0;
  }

  50% {
    top: 10px;
    opacity: 1;
  }

  100% {
    top: 20px;
    opacity: 0;
  }
}

@keyframes scrolldown2 {
  0% {
    top: 0px;
    opacity: 1;
  }

  50% {
    top: 10px;
    opacity: 0;
  }

  100% {
    top: 20px;
    opacity: 1;
  }
}


@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

#intro {
  position: relative;
  min-height: 100vh;
  background: linear-gradient(-45deg, #000000, #212d31, #212d31, #656956);
  background-size: 400% 400%;
  animation: gradient 14s ease-in-out infinite;
}

.intro_wrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.intro_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.intro_text>h1 {
  font-weight: 900;
  font-size: 8vw;
  color: white;
}

.intro_text>h2 {
  font-weight: 700;
  font-size: 3.5vw;
}

.scroll {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.scroll>p {
  font-weight: bold;
  font-size: 1.8vw;
}


.scroll div:nth-child(2) {
  animation: scrolldown 1300ms linear infinite;
}

.scroll div:nth-child(3) {
  animation: scrolldown2 1300ms linear infinite;
}

.scroll div {
  position: relative;
  /* position 속성 추가 */
  width: 30px;
  height: 30px;
  background: url('/public/down-arrow.png') 50% 50% no-repeat;
  background-size: 100% 100%;
}